@charset "utf-8";

/* base variables pulled from theme.yml */
$base-font-family: "Merriweather", serif;
$base-font-size: 1.2em;
$text-color: #191919;
$link-color: #0d6efd;

/* theme colors pulled from config-theme-colors */




$primary: #631919;


$info: #00AEC7;


$pride-gold: #F1B300;


$clearwater: #00AEC7;


$clearwater-light: #8BD5E5;


$payette-blue: #003E51;


$ponderosa-pine: #004E42;


$garnet: #D22630;


$wild-rose: #E10098;


$palouse-green: #00AF66;


$black-color: #191919;


$grey-color: #808080;

$theme-colors: () !default;
$theme-colors: map-merge(
  (
    
    "primary": $primary,
    
    "info": $info,
    
    "pride-gold": $pride-gold,
    
    "clearwater": $clearwater,
    
    "clearwater-light": $clearwater-light,
    
    "payette-blue": $payette-blue,
    
    "ponderosa-pine": $ponderosa-pine,
    
    "garnet": $garnet,
    
    "wild-rose": $wild-rose,
    
    "palouse-green": $palouse-green,
    
    "black-color": $black-color,
    
    "grey-color": $grey-color
    
  ),
  $theme-colors
);
@import "theme-colors", "theme-utilities";


@import "base", "pages", "custom";
